const renderContent = (arr, type) => {
    
}

const Info = ({type}) => {
    if (type == 'UserAgreement') {
      return(
        <div className='info__container'>
          <div className='info__container__text'>
            <h1 className='info__title'>{UserAgreement[0]}</h1>
            <h5 className='info__subtitle'>{UserAgreement[1]}</h5>
            {UserAgreement[2].map((object) => 
            <p className='info__text'>{object}</p>
            )}
          </div>
        </div>
      );
    } else if (type == 'PrivacyPolicy') {
      return(
        <div className='info__container'>
          <div className='info__container__text'>
            <h1 className='info__title'>{PrivacyPolicy[0]}</h1>
            <h5 className='info__subtitle'>{PrivacyPolicy[1]}</h5>
            {PrivacyPolicy[2].map((object) => 
            <p className='info__text'>{object}</p>
            )}
          </div>
        </div>
      );
    } else if (type == 'SiteMaterials') {
      return(
        <div className='info__container'>
          <div className='info__container__text'>
            <h1 className='info__title'>{SiteMaterials[0]}</h1>
            <h5 className='info__subtitle'>{SiteMaterials[1]}</h5>
            {SiteMaterials[2].map((object) => 
            <p className='info__text'>{object}</p>
            )}
          </div>
        </div>
      );
    } else if (type == 'Cookies') {
      return(
        <div className='info__container'>
          <div className='info__container__text'>
            <h1 className='info__title'>{Cookies[0]}</h1>
            <h5 className='info__subtitle'>{Cookies[1]}</h5>
            {Cookies[2].map((object) => 
            <p className='info__text'>{object}</p>
            )}
          </div>
        </div>
      );
    }
  }
  
  export default Info;